@use '../inc/vars' as *;
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: $green;
  z-index: 5;
  display: flex;
  &.hidden {
    transition: all 0.5s;
    opacity: 0;
    visibility: hidden;
  }
  img {
    margin: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    width: 240px;
    height: auto;
    display: flex;
  }
}